<template>
    <div class="PostList">
        <!--    在数据未返回的时候,显示正在加载的gif-->
        <div class="loading" v-if="isLoading">
            <img src="../assets/loading.gif">
        </div>
        <!-- 代表我们的主题列表-->
        <div class="posts" v-else>
            <ul>
                <li>
                    <div class="toobar">
                        <span>全部</span>
                        <span>精华</span>
                        <span>分享</span>
                        <span>问答</span>
                        <span>招聘</span>
                    </div>
                </li>
                <li v-for="post in posts" :key="post.id">
                    <!--posts表示本页存储的所有列表数组-->
                    <!--头像-->
                    <img :src="post.author.avatar_url">
                    <!--回复量和浏览量-->
                    <span class="allcount">
                        <span class="reply_count">{{post.reply_count}}</span>
                       /{{post.visit_count}}
                    </span>
                    <!--置顶精华分类-->
                    <span :class="[{put_good:(post.good == true),put_top:(post.top == true),
                    'topiclist-tab':(post.good != true && post.top != true )}]">
<span>
    {{post | tabFormatter}}
</span>
                    </span>
                    <!-- 标题-->
                    <router-link :to="{name:'post_count',
                    params:{
                        id:post.id,
                        name:post.author.loginname
                    }
                    }"><!--请求一个名字为post_count的路由-->

                        <span>
                        {{post.title}}
                    </span>
                    </router-link>

                    <!--    最终回复时间-->
                    <span class="last_reply">{{post.last_reply_at | formatDate}}</span>
                </li>
                <li>
                    <Pagination @handleList="renderList"></Pagination>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import Pagination from "@/components/Pagination";

    export default {
        name: "PostList",
        data() {
            return {
                isLoading: false,
                posts: [],//代表页面的列表数组
                postpage:1
            }
        },
        components: {
            Pagination
        },
        methods: {
            getData() {//GET请求一一定要加params参数
                this.$http.get('https://cnodejs.org/api/v1/topics', {
                    params:{
                        page: this.postpage,
                        limit: 20,
                    }

                })
                    .then(res => {//eslint-disable-line no-unused-vars
                        this.isLoading = false//加载成功就关闭动画
                        this.posts = res.data.data; //把值复制给post
                    })/*处理成功之后的方法*/
                    .catch(function (err) {
                      //处理返回失败后的问题
                        console.log(err)
                    })
            },
            renderList(value) {//这个方法必须接受一个参数,这个参数从子组件传递过来
               this.postpage = value;
               this.getData();
            }
        },
        //这个方法在页面载入前就开始执行
        beforeMount() {
            this.isLoading = true//加载成功之前显示加载动画
            this.getData()
        }
    }
</script>

<style scoped>
    .PostList {
        background-color: #e1e1e1;
    }

    .posts {
        margin-top: 10px;
    }

    .PostList img {
        height: 30px;
        width: 30px;
        vertical-align: middle;
    }

    ul {
        list-style: none;
        width: 100%;
        max-width: 1344px;
        margin: 0 auto;
    }

    ul li:not(:first-child) { /*除了第一个li*/
        padding: 9px;
        font-size: 15px;
        font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma, "Hiragino Sans GB", STHeiti, sans-serif !important;
        font-weight: 400;
        background-color: white;
        color: #333;
        border-top: 1px solid #f0f0f0;
    }

    li:not(:first-child):hover {
        background: #f5f5f5;;
    }

    li:last-child:hover {
        background: white;
    }

    li span {
        line-height: 30px;
    }

    .allcount {
        width: 80px;
        display: inline-block;
        text-align: center;
        font-size: 12px;
    }

    .reply_count {
        color: #9e78c0;
        font-size: 14px;
    }

    .put_good, .put_top {
        background: #80bd01;
        padding: 2px 4px;
        border-radius: 3px;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        -o-border-radius: 3px;
        color: #fff;
        font-size: 12px;
        margin-right: 10px;
    }

    .topiclist-tab {
        background-color: #e5e5e5;
        color: #999;
        padding: 2px 4px;
        border-radius: 3px;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        -o-border-radius: 3px;
        font-size: 12px;
        margin-right: 10px;
    }

    .last_reply {
        text-align: right;
        min-width: 50px;
        display: inline-block;
        white-space: nowrap;
        float: right;
        color: #778087;
        font-size: 12px;
    }

    .toobar {
        height: 40px;
        background-color: #f5f5f5;
    }

    .toobar span {
        font-size: 14px;
        color: #80bd01;
        line-height: 40px;
        margin: 0 10px;
        cursor: pointer;
    }

    .toobar span:hover {
        color: #9e78c0;
    }

    a {
        text-decoration: none;
        color: black;
    }

    a:hover {
        text-decoration: underline;
    }

    .loading {
        text-align: center;
        padding-top: 300px;
    }
</style>